import React from 'react';

import { Row, Col, Button } from 'antd';
import { Link } from 'react-router-dom';

const items = [
  {
    key: '1',
    icon: <i className="fas fa-chart-pie"></i>,
    title: 'Tp on',
    content: 'Aic group l tp on  thc hin rt nhiu nhng d n lin quan n chuyn i s v thnh ph thng minh, ng gp ln vo trong tin trnh pht trin ca t nc',
  },
  {
    key: '2',
    icon: <i className="fas fa-desktop"></i>,
    title: 'Nhm nghin cu',
    content: 'Nhm chng ti gm cc thnh vin c nhiu nm kinh nghim trong lnh vc cng ngh thng tin, di s hng dn ca cc chuyn gia hng u n t cc trng  i hc v vin nghin cu trn c nc',
  },
  // {
  //   key: '3',
  //   icon: <i className="fas fa-database"></i>,
  //   title: 'Simplified Workflow',
  //   content: 'cu nostro dissentias consectetuer mel. Ut admodum conceptam mei, cu eam tation fabulas abhorreant. His ex mandamus.',
  // },
]

function AppAbout() {
  return (
    <div id="about" className="block aboutBlock">
      <div className="container-fluid">
        <div className="titleHolder">
          <h2>About Us</h2>
          <p>Ban nghin cu v pht trin aic group</p>
        </div>
        <div className="contentHolder">
          <p>Tin phong trong lnh vc chuyn i s v thnh ph thng minh</p>
        </div>
        <Row gutter={[16, 16]}>   
          {items.map(item => {
            return (
              <Col md={{ span: 12 }} key={item.key}>
                <div className="content">
                  <div className="icon">
                    {item.icon}
                  </div>
                  <h3>{item.title}</h3>
                  <p>{item.content}</p>
                </div>
              </Col>
            );
          })}
        </Row>
      </div>
    </div>
  );
}

export default AppAbout;