import { Form, Input, Button, Checkbox } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import '../css/Login.css'
const NormalLoginForm = (props) => {
    const testUser = [
        {
            username: 'phuonglecva',
            password: '1111'
        },
        {
            username: 'haland2k2',
            password: '1111'
        },
        {
            username: 'messi30',
            password: '1111'
        },
        {
            username: 'ronaldo7',
            password: '1111'
        },
    ]
    const onFinish = (values) => {
        const {username, password} = values
        for(let user of testUser) {
            if ((user.username ===  username) && (user.password === password)) {
                props.setToken(true)
                localStorage.setItem("token", true)
                return 
            }
        }
        alert(`User not found.`)
    };
    return (
        <div className="wrapper-login-form">

            <Form
                name="normal_login"
                className="login-form"
                initialValues={{ remember: true }}
                onFinish={onFinish}
            >
                <Form.Item name="title">
                    <h1>ng nhp</h1>
                </Form.Item>
                <Form.Item label="Tn ng nhp" style={{ margin: "0px" }} />
                <Form.Item
                    name="username"
                    rules={[{ required: true, message: 'Please input your Username!' }]}
                >
                    <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="Tn ng nhp" />
                </Form.Item>
                <Form.Item label="Mt khu" style={{ margin: "0px" }} />
                <Form.Item
                    name="password"
                // rules={[{ required: true, message: 'Please input your Password!' }]}
                >
                    <Input
                        prefix={<LockOutlined className="site-form-item-icon" />}
                        type="password"
                        placeholder="Mt khu"
                    />
                </Form.Item>
                <Form.Item>
                    <Form.Item name="remember" valuePropName="checked" noStyle>
                        {/* <Checkbox>Remember me</Checkbox> */}
                    </Form.Item>

                    <a className="login-form-forgot" href="">
                        Qun mt khu
                    </a>
                </Form.Item>

                <Form.Item>
                    <Button type="primary" htmlType="submit" className="login-form-button">
                        ng nhp
                    </Button>
                    Hoc <a href="">ng k</a>
                </Form.Item>
            </Form>
        </div>
    );
};

export default NormalLoginForm;